import React from 'react';
import { Row, Col, Card, Table, Button } from 'react-bootstrap';
import { Link } from "react-router-dom"
import axios from 'axios'
import { confirmAlert } from 'react-confirm-alert';
import 'react-confirm-alert/src/react-confirm-alert.css'; // Import
import Aux from "../../../hoc/_Aux";
import Viewstorekeeper from './ViewStorekeeper';
import i18n from './../../../i18n';

var urlbackend = require('../../../env/env')();


class Liststorekeeper extends React.Component {
    state = {
        storekeepers: [],
    };

    componentDidMount() {

        const config = {
            headers: {
                'Content-Type': 'application/json',
                'Access-Control-Allow-Headers': 'Authorization',
                "x-access-token": localStorage.getItem('token'),
                'Authorization': 'Bearer ' + localStorage.getItem('token'),
            },
        };
        axios.get(urlbackend + "/users/magasinier", config)
            .then(response => {
                console.log("this respon list", response.data)
                this.setState({ storekeepers: response.data.data })

            })
            .catch(e => {
                console.log(e)
            })

    }

    Delette(storekeeper) {
        console.log("hellodelet", storekeeper.id)
        confirmAlert({
            title: i18n.t('title-confirm-delete'),
            message: i18n.t('ask-confirm-delete'),
            buttons: [
                {
                    label: i18n.t('yes'),
                    onClick: () => {
                        axios.delete("http://localhost:5000/api/v1/storekeepers/" + storekeeper.id).then(() => {

                            var array = [...this.state.storekeepers];
                            var index = array.indexOf(storekeeper)
                            if (index !== -1) {
                                array.splice(index, 1);
                                this.setState({ storekeepers: array });
                            }


                        })
                    }
                },
                {
                    label: i18n.t('no'),
                    onClick: null
                }
            ]
        });
        /*  */
    }
    render() {
        return (
            <Aux>
                <Row>
                    <Col>
                        <Card>
                            <Card.Header>
                                <Card.Title as="h5">  <Link to="/Add/storekeeper" >  <Button>{i18n.t('add-store-keeper')}</Button> </Link> </Card.Title>
                            </Card.Header>
                            <Card.Body>
                                <Table striped responsive>
                                    <thead>
                                        <tr>
                                            <th>{i18n.t('fullname')}</th>
                                            <th>{i18n.t('username')}</th>
                                            <th>{i18n.t('phone-number')}</th>
                                            <th>{i18n.t('agency')}</th>
                                            <th>{i18n.t('address')}</th>
                                            <th>{i18n.t('role')}</th>
                                            <th>{i18n.t('action-event')}</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {this.state.storekeepers.map((storekeeper, index) => (

                                            <tr key={storekeeper.id}>
                                               <td> {storekeeper.firstname} {storekeeper.lastname}</td>
                                                <td>{storekeeper.username} </td>
                                                <td>{storekeeper.phone_number} </td>
                                                <td>{storekeeper.agence.name} </td>
                                                <td>{storekeeper.agence.address}  {storekeeper.city} </td>
                                                <td> {storekeeper.descriminator} </td>
                                                <td>  <Button variant="danger"
                                                    onClick={() => this.Delette(storekeeper)}> <span className="fa fa-trash"></span></Button>

                                                        
                                                        <Viewstorekeeper storekeeper={storekeeper} />
                                                </td>
                                            </tr>


                                        ))}
                                    </tbody>
                                </Table>
                            </Card.Body>
                        </Card>
                    </Col>
                </Row>
            </Aux>
        );
    }
}

export default Liststorekeeper;